body {
    font-family: 'gotham','NanumSquare', 돋움, sans-serif; 
}

header {
    background: url(../../public/img/main_img1_pc.jpg) no-repeat;
    background-position: top left;
    background-size: cover;
    width: 100%;
    height: 100vh;
    opacity: 1;
    visibility: inherit;
}
header span {
    margin: 0 0 0 3px;
    font-size: 16px;
}
header .headerbox {
    margin: 0 auto;
    display: flex;
    /* background-color: #000; */
    height: 100px;
    justify-content: space-between;
    width: 1130px;
    align-self: center;
    overflow: hidden;
}
header h1 a {
    width: 108px;
}
/* -----------nav--------------- */
nav .menu {
    display: flex;
    flex-flow: row;
    font-size: 13px;
    font-weight: 600;
}
nav .menu li>a>span {
    font-weight: 400;
    position: absolute;
}
nav .menu>li { text-align: center; }
nav .menu>li:nth-child(1) { width: 112px; }
nav .menu>li:nth-child(2) { width: 107px; }
nav .menu>li:nth-child(3) { width: 107px; }
nav .menu>li:nth-child(4) { width: 103px; }
nav .menu>li:nth-child(5) { width: 116px; }
nav .menu>li:nth-child(6) { 
    width: 135px; 
}
.menu>li> ul.menu_1 {
    display: none;
    
}
.menu>li:hover> ul.menu_1 {
    display: block;
} 

.menu>li:hover> ul.last_menu {
    position: absolute;
    right: 20%;
    width: 60%;
    text-align: left;
    display: flex;
    flex-direction: row;
    background-color: #fff;
    font-weight: 400;
    transition: .4s;
}

.last_menu>li {
    width: 100%;
}

.menu_2_2 {
    display: flex;
    flex-flow: column;
    padding: 20px;
    width: 100%;
}

.menu_2_2>li>a {
    display: block;
    padding: 10px 12px;
    font-size: 12px;
    width: 100%;
}

.menu_2_2>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .4s;
}

header nav .menu>li>a {
    height: 100px;
    line-height: 100px;
    padding: 0 12px;
    color: #fff;
    letter-spacing: 0.7px;
    position: relative;
}
header nav .menu>li> ul {
    position: absolute;
    top: 100px;
    background-color: #fff;
    width: 190px;
    text-align: left;
    font-size: 11px;
    font-weight: 400;
}
header nav .menu>li> ul>li>a {
    padding: 12px 20px;
    color: #999;
    font-weight: 400;
}
header nav .menu>li> ul>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .4s;
}

header nav .menu>li:hover>a {
    color: #d6b161;
    transition: .2s;
} 

header nav .menu>li> ul:nth-child(2)>li {
    position: relative;
    transition: .4s;
}
header nav .menu>li> ul:nth-child(2)>li:first-child strong {
    margin-left: 94px;
    font-weight: 700;
    font-size: 16px;
    transition: .4s;
}
header nav .menu_2_1 {
    position: absolute;
    top: 0; left: 190px;
    width: 190px;
}
header nav .menu2 li:hover .menu_2_1 a {
    background-color: #fff;
    color: #999;
    padding: 10px 0px 10px 22px;
    transition: .4s;
}
header nav .menu2 li:hover .menu_2_1 li:hover a {
    background-color:  #d6b161;
    color: #fff;
    transition: .4s;
}
header nav .menu2>li ul {
    display: none;
}
header nav .menu2>li:hover> ul {
    display: block;
    transition: .4s;
}
/* ----------checkbox---------------- */
header .header_box1 {
    text-align: center;
    position: absolute; 
    top: 55%; left: 50%; transform: translate(-50%, -50%);
}
header .header_box1 h2 {
    font-size: 57px;
    font-family: 'NanumSquare', sans-serif;
    font-weight: 100;
    color: #333333;
    word-spacing: -1px;
    letter-spacing: 4px;
}
header .header_box1 h3 {
    color: #555555;
    font-size: 20px;
    font-family: 'NanumSquare', sans-serif;
    margin-top: 20px;
    letter-spacing: 8px;
    word-spacing: -1px;
    line-height: 30px;
    font-weight: 600;
}
header .header_checkbox {
    margin: 50px auto;
    width: 191px;
    box-sizing: border-box;
}
header .header_checkbox a {
    height: 55px;
    line-height: 25px;
    border: 1px solid #333;
    display: block;
    color: #555;
    padding: 15px 30px;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1px;
}
header .header_checkbox:hover a {
    background-color: #333;
    color: #fff;
    font-weight: 600;
    transition: .4s;
}
header .header_checkbox a img {
    width: 17px;
    height: 17px;
    transform: translateY(3px);
}
header .header_checkbox a img {
    filter: invert(34%) sepia(0%) saturate(0%) hue-rotate(171deg) brightness(91%) contrast(89%);
}
header .header_checkbox:hover a img {
    filter: invert(100%) sepia(0%) saturate(7495%) hue-rotate(293deg) brightness(104%) contrast(103%);
    
}


